<template>
  <div class="category">
    <a href="javascript:void(0);" :class="{active:item.id === Number(songType)}" v-for="(item,index) in songTypeList"
       :key="item.id" @click="songTypeIndex($event)" :data-type="item.id">{{item.text}}</a>
  </div>
</template>

<script>
  export default {
    props: ['songType'],
    name: "Category",
    data(){
      return {
        songTypeList: [
          {
            id: 1,
            text: '新歌榜'
          },
          {
            id: 2,
            text: '热歌榜'
          },
          {
            id: 11,
            text: '摇滚榜'
          },
          {
            id: 12,
            text: '爵士'
          },
          {
            id: 16,
            text: '流行'
          },
          {
            id: 21,
            text: '欧美金曲榜'
          },
          {
            id: 22,
            text: '经典老歌榜'
          },
          {
            id: 23,
            text: '情歌对唱榜'
          },
          {
            id: 24,
            text: '影视金曲榜'
          },
          {
            id: 25,
            text: '网络歌曲榜'
          }
        ]
      }
    },
    methods: {
      songTypeIndex($event){
        this.$emit('getChild', $event.target.getAttribute('data-type'));
      }
    }
  }
</script>

<style scoped>
  .category {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 34px;
    background-color: #C20C0C;
    border-bottom: 1px solid #C20C0C;
  }

  .category a {
    color: #fff;
    padding: 4px 13px;
    font-size: 12px;
    border-radius: 20px;
    line-height: 17px;
  }
  .category a.active,
  .category a:hover {
    background-color: #9B0909;
  }
</style>



