<template>
  <div class="tabbar">
    <router-link to="/main/home" :class="{active:this.$route.path === '/main/home'}">首页</router-link>
    <router-link to="/main/search" :class="{active:this.$route.path === '/main/search'}">搜索</router-link>
    <!--<router-link to="/main/login" :class="{active:this.$route.path === '/main/login'}">登录</router-link>-->
  </div>
</template>

<script>
  // 路由跳转
  // patams传参 ，路径不能使用path 只能使用name,不然获取不到传的数据
  // query传参
  // name属性值为：路由文件中定义的'name'值
  // this.$router.push({name: 'login', params: {name: 'renmeiling'}

  export default {
    name: 'TabBar'
  }
</script>

<style scoped>
  .tabbar {
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 45px;
    line-height: 45px;
    font-size: 14px;
    text-align: center;
    background-color: #333333;
    border-bottom: 1px solid #000;
  }

  .tabbar a {
    padding: 0 19px;
    color: #ccc;
  }

  .tabbar a:hover,
  .tabbar a.active {
    color: #fff;
    background-color: #000;
  }

</style>
<!--
容器属性：
  flex-direction：决定主轴的方向（即项目的排列方向）
  flex-wrap：定义换行情况
  flex-flow：flex-direction和flex-wrap的简写，默认row nowrap
  justify-content：定义项目在主轴上的对齐方式。
  align-item：定义在交叉轴上的对齐方式
  align-content：定义多根轴线的对齐方式

项目属性：
  order：定义项目的排列顺序。
  flex-grow：定义项目的放大比例
  flex-shrink：定义项目的缩小比例
  flex-basis：定义在分配多余空间之前，项目占据的主轴空间。
  flex：flex-grow，flex-shrink和flex-basis的简写
  align-self：允许单个项目与其他项目有不一样的对齐方式
-->



