<template>
  <div id="home">
    <Category @getChild="songTypeIndex" :song-type="songType"/>
    <div class="songs-content">
      <SongsCenter :song-list="songList" :play-sond-id="playSondId" @getSongsCenterData="sendData"/>
      <SongInfo :singer-data="singerData" :song-lrc-data="songLrcData" :song-link="songLink"
                @listPlaySongs="listPlaySongs" ref="mySongInfo"/>
    </div>
  </div>
</template>

<script>
  import Category from '../basic/Category'
  import SongsCenter from './SongsCenter'
  import SongInfo from './SongInfo'
  // import homeFetch from '../../fetch/home'

  // var homeWebServer = null;

  export default {
    name: 'Home',
    data(){
      return {
        // 歌曲列表播放的索引
        listIndex: 0,
        // 播放、获取歌词的歌曲id
        playSondId: 0,
        // 获取歌手信息的id
        playTingUid: 0,
        songType: 1,
        songSize: 19,
        songOffset: 0,
        songList: [],
        // 歌手信息
        singerData: {},
        // 歌词信息
        songLrcData: {},
        songLink: ''
      }
    },
    mounted(){
      // homeWebServer = new homeFetch(this.HOME);
      if(this.$route.params && this.$route.params.songs){
        this.songList = this.$route.params.songs;
      }else{
        this.getSongList();
      }
    },
    methods: {
      songTypeIndex(data){
        this.songType = data;
        this.getSongList();
      },
      sendData(songId, tingUid){
        function getInArrayIndex(item, ary){
          if(!item){
            return -1;
          }
          if(!ary.length){
            return -1;
          }
          for(var i = 0; i < ary.length; i++){
            var cur = ary[i];
            if(({}).toString.call(cur) === '[object Object]'){
              for(var key in cur){
                if(cur.hasOwnProperty(key)){
                  if(cur[key] === item){
                    return i;
                  }
                }
              }
            }else{
              if(cur === item){
                return i;
              }
            }
          }
          return -1;
        }

        this.listIndex = getInArrayIndex(songId, this.songList);
        this.playSondId = songId;
        this.playTingUid = tingUid;
        tingUid && this.getSingerInfo();
        this.getSongLrcInfo();
        this.playSong();
      },
      // 获取歌曲列表
      getSongList(){
        var _this = this;
        /*homeWebServer.getData(`method=baidu.ting.billboard.billList&type=${this.songType}&size=${this.songSize}&offset=${this.songOffset}`).then(function(response){
          if(response.status === 200){
            _this.songList = response.data['song_list'];
          }
        }).catch(error => {
          _this.songList = [];
          console.dir(error);
        });*/
        $.ajax({
          url: `${this.HOME}method=baidu.ting.billboard.billList&type=${this.songType}&size=${this.songSize}&offset=${this.songOffset}`,
          type: 'get',
          cache: false,
          dataType: 'jsonp',
          success: function(data){
            _this.songList = data['song_list'];
          },
          error: function(error){
            _this.songList = [];
            console.dir(error);
          }
        });

      },
      // 获取歌手信息
      getSingerInfo(){
        var _this = this;
        /*homeWebServer.getData(`method=baidu.ting.artist.getInfo&tinguid=${this.playTingUid}`).then(function(response){
          if(response.status === 200){
            _this.singerData = response.data;
          }
        }).catch(function(error){
          _this.singerData = {};
          console.log(`%c 获取歌手信息失败`, "color:#ff0000");
        });*/

        $.ajax({
          url: `${this.HOME}method=baidu.ting.artist.getInfo&tinguid=${this.playTingUid}`,
          type: 'get',
          cache: false,
          dataType: 'jsonp',
          success: function(data){
            _this.singerData = data;
          },
          error: function(error){
            _this.singerData = {};
            console.log(`%c 获取歌手信息失败`, "color:#ff0000");
          }
        })
      },
      // 获取歌曲歌词
      getSongLrcInfo(){
        var _this = this;
        /*homeWebServer.getData(`method=baidu.ting.song.lry&songid=${this.playSondId}`).then(function(response){
          if(response.status === 200){
            _this.songLrcData = response.data;
          }
        }).catch(function(error){
          _this.songLrcData = {};
          console.log(`%c 获取歌曲歌词信息失败`, "color:#ff0000");
        });*/

        $.ajax({
          url: `${this.HOME}method=baidu.ting.song.lry&songid=${this.playSondId}`,
          type: 'get',
          cache: false,
          dataType: 'jsonp',
          success: function(data){
            _this.songLrcData = data;
          },
          error: function(error){
            _this.songLrcData = {};
            console.log(`%c 获取歌曲歌词信息失败`, "color:#ff0000");
          }
        })
      },
      // 播放歌曲
      playSong(){
        var _this = this;
        /*homeWebServer.getData(`method=baidu.ting.song.play&songid=${this.playSondId}`).then(function(response){
          if(response.status === 200){
            _this.songLink = response.data.bitrate.file_link;
          }
        }).catch(function(error){
          _this.songLink = '';
          console.log(`%c 获取歌曲链接失败`, "color:#ff0000");
        });*/
        $.ajax({
          url: `${this.HOME}method=baidu.ting.song.play&songid=${this.playSondId}`,
          type: 'get',
          cache: false,
          dataType: 'jsonp',
          success: function(data){
            _this.songLink = data['bitrate']['file_link'];
          },
          error: function(error){
            _this.songLink = '';
            console.log(`%c 获取歌曲链接失败`, "color:#ff0000");
          }
        })
      },
      // 列表播放歌曲
      listPlaySongs(){
        this.listIndex++;
        if(this.listIndex >= this.songList.length){
          this.listIndex = 0;
        }
        this.playSondId = this.songList[this.listIndex]['song_id'];
        this.playTingUid = this.songList[this.listIndex]['ting_uid'];
        this.playTingUid && this.getSingerInfo();
        this.getSongLrcInfo();
        this.playSong();
        this.$refs.mySongInfo.playing();
        console.log(`现在播放第${this.listIndex + 1}首歌`);
      }
    },
    components: {
      Category,
      SongsCenter,
      SongInfo
    }
  }
</script>

<style scoped>
  .songs-content {
    height: 570px;
    overflow: hidden;
    border-top: 2px solid #fff;
    border-bottom: 5px solid #C20C0C;
    -webkit-box-sizing: content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box;
  }
</style>



