<template>
  <div>
    <div class="search">
      <div style="margin-bottom:10px;text-align:center;">
        <img src="../../../static/imges/music.icon.png" width="100" alt="">
      </div>
      <input type="text" placeholder="请输入搜索关键字" v-model="query"/>
      <span><input type="button" value="搜索" @click="queryHandle()"/></span>
    </div>
    <div class="search-footer">云中白鹤 @ 2018-12-04 &copy; music</div>
  </div>
</template>

<script>
  // import Server from '../../fetch/home'

  var service = null;

  export default {
    name: "Search",
    data(){
      return {
        query: '',
        isQueryed: false
      }
    },
    mounted(){
      // service = new Server(this.HOME);
      var _this = this;
      $(document).on('keyup', function(event){
        event = event || window.event;
        if(event.keyCode === 13){
          _this.queryHandle();
        }
      });
    },
    beforeRouteEnter: function(to, from, next){
      to.meta.keepAlive = true;
      next();
    },
    methods: {
      queryHandle(){
        var _this = this;
        _this.isQueryed = true;
        /*service.getData(`method=baidu.ting.search.catalogSug&query=${this.query}`).then(
          function(response){
            if(response.status === 200){
              var result = response.data;
              var songs = [];
              for(var i = 0; i < result.song.length; i++){
                var item = result.song[i];
                item['song_id'] = item['songid'];
                item['title'] = item['songname'];
                songs.push(item);
              }
              _this.$router.push({
                name: 'home',
                params: {
                  "songs": songs
                }
              });
              _this.songList = songs;
            }
            _this.query = '';
            _this.isQueryed = false;
          }
        ).catch(function(error){
          console.log('%c 搜索歌曲失败', 'color:#ff0000');
        });*/
        $.ajax({
          url: `${this.HOME}method=baidu.ting.search.catalogSug&query=${this.query}`,
          type: 'get',
          cache: false,
          dataType: 'jsonp',
          success: function(data){
            if(data['error_code'] === 22000){
              var songs = [];
              for(var i = 0; i < data['song'].length; i++){
                var item = data['song'][i];
                item['song_id'] = item['songid'];
                item['title'] = item['songname'];
                songs.push(item);
              }
              _this.$router.push({
                name: 'home',
                params: {
                  "songs": songs
                }
              });
              _this.songList = songs;
            }
            _this.query = '';
            _this.isQueryed = false;
          },
          error: function(error){
            console.log('%c 搜索歌曲失败', 'color:#ff0000');
          }
        })
      }
    },
    beforeRouteLeave: function(to, from, next){
      // 设置下一个路由的 meta
      to.meta.keepAlive = !this.isQueryed;
      next();
    }
  }
</script>

<style scoped>
  .search {
    margin: 100px auto 0;
    width: 582px;
    overflow: hidden;
  }

  .search > input {
    padding: 9px 7px;
    width: 480px;
    height: 40px;
    outline: none;
    border: 1px solid #B8B8B8;
  }

  .search > input,
  .search > span {
    float: left;
  }

  .search > input:focus {
    border-color: #3388FF;
  }

  .search span {
    width: 102px;
    height: 40px;
    border: 1px solid #38f;
    border-bottom: 1px solid #2e7ae5;
    background-color: #38f;
  }

  .search span:hover {
    border-bottom: 1px solid #2771d9;
  }

  .search span input {
    cursor: pointer;
    width: 102px;
    height: 38px;
    line-height: 38px;
    padding: 0;
    border: 0;
    background: none;
    background-color: #38f;
    font-size: 16px;
    color: white;
    box-shadow: none;
    font-weight: normal;
    outline: none;
  }

  .search span input:hover {
    height: 39px;
    _height: 38px;
    background-color: #317ef3;
    border-bottom: 1px solid #2868c8;
    box-shadow: 1px 1px 1px #ccc;
  }

  .search-footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    line-height: 40px;
    color: #fff;
    text-align: center;
    background-color: #434343;
  }
</style>



