<template>
  <div class="song-info">
    <div class="song-info-siger">
      <p>歌手信息</p>
      <p class="siger-logo">
        <img v-if="singerData['avatar_s1000']" :src="singerData['avatar_s1000']" alt="歌手照片" width="100"/>
      </p>
      <p>地区：{{(singerData['country'] === '香港' || singerData['country'] === '澳门' ||
        singerData['country'] === '台湾') ?
        '中国' + singerData['country'] : singerData['country']}}</p>
      <p>姓名：{{singerData['name']}}</p>
      <p>公司：{{singerData['company']}}</p>
      <p>生日：{{singerData['birth']}}</p>
      <p style="height:300px;overflow:hidden;">简介：{{singerData['intro']}}</p>
      <!-- 没有歌曲播放 -->
      <div class="song-no-played" v-if="!singerData['ting_uid']"></div>
    </div>
    <div class="song-info-lrc">
      <p>{{songLrcData['title'] ? songLrcData['title'] : '歌词信息'}}</p>
      <div class="lrc-content-wrap">
        <ul id="lrcContent" class="lrc-content">
          <li v-for="item in lrcData" :data-minutes="item.minutes" :data-seconds="item.seconds">{{item.text}}</li>
        </ul>
      </div>
      <!-- 没有歌曲播放 -->
      <div class="song-no-played" v-if="!lrcData.length"></div>
    </div>
  </div>
</template>

<script>
  import eventBus from '../../assets/eventBus';

  export default {
    props: ['singerData', 'songLrcData', 'songLink', 'playSondId'],
    name: "SongInfo",
    data(){
      return {
        lrcData: [],
        audioBox: null,
        autoTimer: null
      }
    },
    watch: {
      songLrcData: function(newVal, oldVal){
        this.lrcData = this.analysisLrc(this.songLrcData['lrcContent']);
      },
      songLink: function(newVal, oldVal){
        this.audioBox.src = this.songLink;
        this.audioBox.play();
      }
    },
    mounted(){
      this.audioBox = document.getElementById('audioBox');
      this.lrcContent = document.getElementById('lrcContent');
      var _this = this;
      eventBus.$on('isSongCanPlay', function(data){
        if(data === 'true'){
          _this.audioBox.play();
          _this.playing();
        }else{
          clearInterval(_this.autoTimer);
          _this.audioBox.pause();
        }
      });
    },
    methods: {
      // 解析歌词
      analysisLrc(data){
        var ary = [];
        data.replace(/[\r\n\s&nbsp;]/igm, '').replace(/\[(\d+):(\d+)\.\d+]([^\[]+)?/igm, function(...arg){
          var [, minutes, seconds, text] = arg;
          if(text){
            ary.push({
              minutes: minutes,
              seconds: seconds,
              text: text
            });
          }
        });
        return ary;
      },
      // 歌曲播放以及歌词对应
      playing(){
        var _this = this;
        clearInterval(_this.autoTimer);
        this.autoTimer = setInterval(function(){
          var duration = _this.audioBox.duration;
          var curTime = _this.audioBox.currentTime;
          _this.lyricSpondence(duration, curTime);
          // 播放完成后暂停
          if(curTime >= duration){
            clearInterval(_this.autoTimer);
            $(_this.lrcContent).find('li').removeClass('active');
            $(_this.lrcContent).attr('data-y', 0).css('transform', `translateY(0)`);
            // 触发Home.vue 的方法
            _this.$emit('listPlaySongs');
          }
        }, 1000);
      },
      // 歌词对应
      lyricSpondence(duration, curTime){
        var minutes = Math.floor(curTime / 60);
        var seconds = Math.ceil(curTime - minutes * 60);
        minutes < 10 ? minutes = '0' + minutes : null;
        seconds < 10 ? seconds = '0' + seconds : null;
        var liList = $(this.lrcContent).find('li');
        var curLi = liList.filter(`[data-minutes="${minutes}"]`).filter(`[data-seconds="${seconds}"]`);
        if(curLi.length){
          curLi.addClass('active').siblings().removeClass('active');
          var index = curLi.index();
          if(index >= 9){
            var y = ($(this.lrcContent).attr('data-y') || 0) - 30;
            $(this.lrcContent).attr('data-y', y).css('transform', `translateY(${y}px)`);
          }
        }
      }
    }
  }
</script>

<style scoped>
  .song-info {
    float: right;
    width: 50%;
    min-height: 570px;
  }

  .song-info > div {
    position: relative;
    float: left;
    padding: 0 10px;
    width: 50%;
    min-height: 570px;
    line-height: 30px;
    text-align: center;
  }

  .song-info-siger > p:first-child {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    color: #fff;
    background-color: #C20C0C;
  }

  .siger-logo {
    margin-top: 30px;
    padding: 5px 0;
  }

  .siger-logo img {
    vertical-align: middle;
    -webkit-border-radius: 100%;
    -moz-border-radius: 100%;
    border-radius: 100%;
  }

  .song-info-lrc {
    border-left: 2px solid #C10D0C;
  }

  .song-info-lrc > p {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #C20C0C;
    color: #fff;
    overflow: hidden;
    white-space: nowrap;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
  }

  .lrc-content-wrap {
    margin-top: 30px;
    height: 540px;
    overflow: hidden;
  }

  .lrc-content {
    transition: all .3s linear;
  }

  .lrc-content li {
    list-style: none;
    height: 30px;
    white-space: nowrap;
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    transition: all .3s linear;
  }

  .lrc-content li.active {
    color: #C20C0C;
    font-weight: bold;
  }
</style>



