<template>
  <div class="songlist">
    <div class="songlist-list" v-for="(item,index) in songList" :key="item.song_id"
         :class="{active:playSondId === item.song_id}">
      <div>
        <span :class="{'song-playing':playSondId === item.song_id}">{{item.title.replace(/[（）《》]/g, '')}}</span>
        <span style="float:right;padding-right:10px;padding-left:0;">{{item.author}}</span>
      </div>
      <div>
        <a href="javascript:void(0);" :data-songid="item.song_id" :data-tinguid="item.ting_uid"
           @click="sendData(item.song_id,item.ting_uid,$event)" class="song-play-pause"
           :class="{'song-played':playSondId === item.song_id}">
        </a>
      </div>
    </div>
    <!-- 没有歌曲播放 -->
    <div class="song-no-played" v-if="!songList.length"></div>
  </div>
</template>

<script>
  import eventBus from '../../assets/eventBus';

  export default {
    props: ['songList', 'playSondId'],
    name: "SongsCenter",
    methods: {
      sendData(songId, tingUid, $event){
        // 如果点击的为 “播放中”，则暂停歌曲播放
        eventBus.$emit('isSongCanPlay', 'true');
        // 播放中
        if($($event.target).hasClass('song-played')){
          $($event.target).removeClass('song-played').addClass('song-paused');
          eventBus.$emit('isSongCanPlay', 'false');

        }else if($($event.target).hasClass('song-paused')){ // 暂停
          $($event.target).removeClass('song-paused').addClass('song-played');
          eventBus.$emit('isSongCanPlay', 'true');

        }else{
          $('#lrcContent').find('li').removeClass('active');
          $('#lrcContent').attr('data-y', 0).css('transform', `translateY(0)`);
          this.$emit('getSongsCenterData', songId, tingUid);
        }
      }
    }
  }
</script>
<style scoped>
  .songlist {
    position: relative;
    float: left;
    width: 50%;
    min-height: 570px;
    background-color: #F4F4F4;
  }

  .song-no-played {
    top: 0;
    height: 100%;
    border-right: 2px solid #C20C0C;
  }

  .songlist-list {
    padding: 0 10px;
    line-height: 30px;
    white-space: nowrap;
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
  }

  .songlist-list:nth-child(odd) {
    background-color: #E8E8E8;
  }

  .songlist-list:nth-child(even) {
    background-color: #F4F4F4;
  }

  .songlist-list.active span,
  .songlist-list.active a {
    color: #C20C0C;
    font-weight: bold;
  }

  .songlist-list > div {
    float: left;
    white-space: nowrap;
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
  }

  .songlist-list > div:first-child {
    position: relative;
    width: 95%;
  }

  .songlist-list > div:last-child {
    width: 5%;
  }

  .songlist-list span {
    padding-left: 20px;
  }

  .songlist-list a {
    display: block;
    width: 100%;
    height: 30px;
  }

  .songlist-list a:hover {
    color: #C20C0C;
  }
</style>



