import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/main/Main'
import Login from '@/components/login/Login'
import Home from '@/components/page/Home'
import Search from '@/components/page/Search'

Vue.use(Router);

export default new Router({
  // mode: 'history',
  routes: [
    // 重定向
    {
      path: '/',
      redirect: '/main'
    },
    {
      path: '/main',
      name: 'main',
      component: Main,
      children: [
        {
          path: '/main',
          redirect: '/main/home'
        },
        {
          path: '/main/home',
          name: 'home',
          component: Home,
          meta: {
            keepAlive: true
          }
        },
        {
          path: '/main/login',
          name: 'login',
          component: Login
        },
        {
          path: '/main/search',
          name: 'search',
          component: Search,
          meta: {
            keepAlive: false
          }
        }
      ]
    }
  ]
})



